import React from 'react'
import Link from 'gatsby-link'

export default (props) => (
  <Link to={props.link} className="downloadBtn">
    <i className={`${props.icon} fa-3x`} aria-hidden="true" />
    <span>{props.desc}</span>
  </Link>
)



